.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

body {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.statsTable {
  margin-left: 50px;
  margin-right: 50px;
  color:#DDDDDD;
}

h3 {
  margin: 0;
  margin-bottom: 15px;
}

h6 {
  margin: 0;
  margin-bottom: 15px;
  color:#DDDDDD;
}

.statsTable td {
  padding-left: 17px;
  padding-right: 17px;
  min-width: 50px;
  padding-top: 4px;
  padding-bottom: 4px;
  background-color: #EEEEFF11;
}

.statsTable th {
  cursor: pointer;
}

#container {
  display: flex;
  flex-direction: column;
  padding-top: 60px;
  width: 900px;
  align-items: flex-start;
}

#iStatsContainer {
  display: flex;
  flex-direction: row;
}

#iStatsContainer p {
  font-size: 12pt;
  margin: 0;
  font-family: 'Open Sans';
}

#legend {
  text-align: left;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

#legend td {
  margin-bottom: 5px;
  padding-right: 10px;
}

.legendAcronym {
  font-weight: bold;
}
